.slider {
  position: relative;
}

.eventSlider {
  position: absolute;
  height: 40px;
  display: flex;
  align-items: center;
  border: none;
  cursor: pointer;
  padding: 0;
  background-color: transparent;
}

.eventSlider::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 60px;
  height: 60px;
  z-index: 10;
  background-image: url("/public/sportsnetwork.png");
}

.eventBar {
  height: 3px;
  background-color: rgb(33, 186, 69);
  width: 100%;
}
